.portfolio-root {
  position: relative;
  z-index: 1;
  min-height: 100vh;
  color: #f3f3f3;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  padding: 0;
  margin: 0;
}

.hero {
  text-align: center;
  padding: 3rem 1rem 2rem 1rem;
  background: linear-gradient(90deg, #0f2027 0%, #2c5364 100%);
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  box-shadow: 0 4px 24px rgba(0,0,0,0.2);
  position: relative;
  z-index: 1;
}
.hero h1 {
  font-size: 3rem;
  margin: 0;
  letter-spacing: 2px;
  font-weight: 700;
}
.subtitle {
  font-size: 1.3rem;
  color: #61dafb;
  margin: 0.5rem 0 1.5rem 0;
}
.hero-links {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.hero-links a {
  color: #fff;
  background: #61dafb;
  padding: 0.5rem 1.2rem;
  border-radius: 2rem;
  text-decoration: none;
  font-weight: 500;
  transition: background 0.2s, color 0.2s, transform 0.2s;
  box-shadow: 0 2px 8px rgba(97,218,251,0.15);
}
.hero-links a:hover {
  background: #21a1f3;
  color: #fff;
  transform: translateY(-2px) scale(1.05);
}

section {
  margin: 2.5rem auto;
  max-width: 900px;
  padding: 1.5rem 1rem;
  background: rgba(30, 34, 40, 0.85);
  border-radius: 1.5rem;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  will-change: opacity, transform;
}
section h2 {
  color: #61dafb;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: 600;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.5rem;
}
.skill-category {
  background: #23272f;
  border-radius: 1rem;
  padding: 1rem 1.2rem;
  box-shadow: 0 1px 8px rgba(97,218,251,0.05);
  will-change: opacity, transform;
}
.skill-category h3 {
  color: #21a1f3;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
}
.skill-category ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.skill-category li {
  margin-bottom: 0.3rem;
  font-size: 1rem;
}

.exp-card {
  background: #23272f;
  border-radius: 1rem;
  margin-bottom: 1.5rem;
  padding: 1.2rem 1.5rem;
  box-shadow: 0 1px 8px rgba(97,218,251,0.05);
  transition: transform 0.2s;
  will-change: opacity, transform;
}
.exp-card:hover {
  transform: translateY(-3px) scale(1.01);
}
.exp-card h3 {
  margin: 0 0 0.3rem 0;
  font-size: 1.2rem;
  color: #fff;
}
.exp-card h3 span {
  color: #61dafb;
  font-weight: 400;
  font-size: 1rem;
}
.exp-meta {
  color: #b0b0b0;
  font-size: 0.95rem;
  margin-bottom: 0.7rem;
}
.exp-card ul {
  margin: 0;
  padding-left: 1.2rem;
}
.exp-card li {
  margin-bottom: 0.5rem;
  line-height: 1.5;
}

.edu-block {
  background: #23272f;
  border-radius: 1rem;
  padding: 1rem 1.2rem;
  margin-bottom: 1rem;
  box-shadow: 0 1px 8px rgba(97,218,251,0.05);
}
.edu-block h3 {
  margin: 0 0 0.3rem 0;
  color: #fff;
  font-size: 1.1rem;
}
.edu-period {
  color: #b0b0b0;
  font-size: 0.95rem;
}
.certificates {
  margin-top: 1rem;
}
.certificates h4 {
  color: #21a1f3;
  margin-bottom: 0.5rem;
}
.certificates ul {
  list-style: disc inside;
  margin: 0;
  padding-left: 1.2rem;
}
.certificates li {
  margin-bottom: 0.3rem;
  font-size: 1rem;
}

/* Sticky Navigation Bar */
.sticky-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 100;
  background: rgba(36, 40, 50, 0.92);
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 0.7rem 0;
  transition: background 0.3s, box-shadow 0.3s;
  backdrop-filter: blur(8px);
}
.sticky-nav a {
  color: #61dafb;
  font-weight: 600;
  text-decoration: none;
  font-size: 1.1rem;
  padding: 0.3rem 1rem;
  border-radius: 1.5rem;
  transition: background 0.2s, color 0.2s;
}
.sticky-nav a.active, .sticky-nav a:hover {
  background: #21a1f3;
  color: #fff;
}

/* Animated Hero Background */
.hero-bg {
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  z-index: 0;
  overflow: hidden;
  pointer-events: none;
}
.floating-shape {
  position: absolute;
  border-radius: 50%;
  opacity: 0.18;
  filter: blur(2px);
  animation: floatShape 12s ease-in-out infinite alternate;
}
.floating-shape.shape1 {
  width: 180px; height: 180px;
  background: #61dafb;
  top: 10%; left: 8%;
  animation-delay: 0s;
}
.floating-shape.shape2 {
  width: 120px; height: 120px;
  background: #21a1f3;
  top: 60%; left: 80%;
  animation-delay: 2s;
}
.floating-shape.shape3 {
  width: 90px; height: 90px;
  background: #fff;
  top: 30%; left: 60%;
  animation-delay: 4s;
}
.floating-shape.shape4 {
  width: 60px; height: 60px;
  background: #ffb347;
  top: 70%; left: 20%;
  animation-delay: 6s;
}
@keyframes floatShape {
  0% { transform: translateY(0) scale(1); }
  100% { transform: translateY(-40px) scale(1.1); }
}

/* Section transitions */
section, .exp-card, .skill-category {
  will-change: opacity, transform;
}

/* Responsive nav */
@media (max-width: 600px) {
  .hero h1 {
    font-size: 2.1rem;
  }
  section {
    padding: 1rem 0.3rem;
  }
  .skills-grid {
    grid-template-columns: 1fr;
  }
  .sticky-nav {
    gap: 0.5rem;
    font-size: 0.95rem;
    padding: 0.5rem 0;
  }
  .sticky-nav a {
    padding: 0.2rem 0.7rem;
    font-size: 1rem;
  }
}

/* Contact Me Section */
.contact-section {
  margin-top: 2.5rem;
  background: rgba(30, 34, 40, 0.95);
  border-radius: 1.5rem;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  padding: 2rem 1rem;
  text-align: center;
}
.contact-section h2 {
  color: #61dafb;
  margin-bottom: 1.2rem;
  font-size: 2rem;
  font-weight: 600;
}
.contact-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
  margin-top: 1rem;
}
.contact-link {
  display: flex;
  align-items: center;
  color: #fff;
  background: #23272f;
  padding: 0.7rem 1.5rem;
  border-radius: 2rem;
  font-size: 1.1rem;
  text-decoration: none;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(97,218,251,0.10);
  transition: background 0.2s, color 0.2s, transform 0.2s;
}
.contact-link:hover {
  background: #21a1f3;
  color: #fff;
  transform: translateY(-2px) scale(1.04);
}
@media (max-width: 600px) {
  .contact-section {
    padding: 1.2rem 0.3rem;
  }
  .contact-link {
    font-size: 1rem;
    padding: 0.6rem 1rem;
  }
}

.floating-name {
  position: absolute;
  z-index: 0;
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
  opacity: 0.7;
  pointer-events: none;
  user-select: none;
  text-shadow: 0 4px 24px rgba(97,218,251,0.18), 0 1px 8px rgba(0,0,0,0.12);
  letter-spacing: 2px;
  filter: blur(0.5px) brightness(1.1);
  transition: opacity 0.3s;
}
@media (max-width: 600px) {
  .floating-name {
    font-size: 1.1rem !important;
  }
}

/* Animated full-page background */
.animated-bg {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
  pointer-events: none;
  overflow: hidden;
  background: linear-gradient(120deg, #232526 0%, #414345 100%);
  animation: bgGradientMove 16s ease-in-out infinite alternate;
}
@keyframes bgGradientMove {
  0% { background-position: 0% 50%; filter: brightness(1.1) saturate(1.1); }
  100% { background-position: 100% 50%; filter: brightness(1.2) saturate(1.3); }
}

.code-float {
  position: absolute;
  color: #61dafb;
  font-family: 'Fira Mono', 'Menlo', 'Monaco', 'Consolas', monospace;
  font-size: 1.1rem;
  opacity: 0.13;
  pointer-events: none;
  user-select: none;
  white-space: pre;
  animation: codeFloatAnim 18s linear infinite;
}
.code-float.c1 { left: 10vw; top: 20vh; font-size: 1.3rem; animation-delay: 0s; }
.code-float.c2 { left: 70vw; top: 60vh; font-size: 1.1rem; animation-delay: 3s; }
.code-float.c3 { left: 40vw; top: 80vh; font-size: 1.5rem; animation-delay: 6s; }
.code-float.c4 { left: 80vw; top: 30vh; font-size: 1.2rem; animation-delay: 9s; }
.code-float.c5 { left: 20vw; top: 70vh; font-size: 1.1rem; animation-delay: 12s; }
@keyframes codeFloatAnim {
  0% { transform: translateY(0) scale(1) rotate(-2deg); opacity: 0.13; }
  50% { transform: translateY(-60px) scale(1.08) rotate(2deg); opacity: 0.18; }
  100% { transform: translateY(0) scale(1) rotate(-2deg); opacity: 0.13; }
}

.hero-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  min-height: 260px;
}
.hero-left {
  flex: 1 1 60%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  min-width: 0;
}
.hero-right {
  flex: 1 1 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 0;
}
.avatar-illustration {
  background: linear-gradient(135deg, #232526 60%, #61dafb 100%);
  border-radius: 50%;
  box-shadow: 0 4px 24px rgba(97,218,251,0.18), 0 1px 8px rgba(0,0,0,0.12);
  padding: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  margin-left: 2rem;
}
@media (max-width: 900px) {
  .hero-content {
    flex-direction: column;
    align-items: center;
    min-height: unset;
  }
  .hero-left, .hero-right {
    width: 100%;
    align-items: center;
    justify-content: center;
    margin: 0;
  }
  .avatar-illustration {
    margin: 2rem 0 0 0;
    width: 160px;
    height: 160px;
    padding: 0.7rem;
  }
}

.side-panel {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(36, 40, 50, 0.92);
  border-radius: 1.5rem;
  box-shadow: 0 2px 16px rgba(97,218,251,0.08);
  padding: 1.2rem 0.7rem;
  gap: 1.1rem;
  transition: background 0.3s, box-shadow 0.3s;
  min-width: 56px;
}
.tech-stack-panel {
  left: 2vw;
  animation: sidePanelInLeft 1.2s cubic-bezier(0.77,0,0.175,1) 0.2s both;
}
.quick-facts-panel {
  right: 2vw;
  min-width: 120px;
  animation: sidePanelInRight 1.2s cubic-bezier(0.77,0,0.175,1) 0.2s both;
}
@keyframes sidePanelInLeft {
  0% { opacity: 0; transform: translate(-40px, -50%); }
  100% { opacity: 1; transform: translateY(-50%); }
}
@keyframes sidePanelInRight {
  0% { opacity: 0; transform: translate(40px, -50%); }
  100% { opacity: 1; transform: translateY(-50%); }
}
.tech-icon {
  color: #61dafb;
  font-size: 2.1rem;
  background: #23272f;
  border-radius: 50%;
  padding: 0.5rem;
  margin: 0.1rem 0;
  box-shadow: 0 2px 8px rgba(97,218,251,0.10);
  transition: background 0.2s, color 0.2s, transform 0.2s;
  cursor: pointer;
  animation: techIconPop 0.7s cubic-bezier(0.77,0,0.175,1) both;
}
.tech-icon:hover {
  background: #21a1f3;
  color: #fff;
  transform: scale(1.13) rotate(-8deg);
}
@keyframes techIconPop {
  0% { opacity: 0; transform: scale(0.7) translateY(20px); }
  100% { opacity: 1; transform: scale(1) translateY(0); }
}
.quick-facts-title {
  color: #61dafb;
  font-weight: 700;
  font-size: 1.1rem;
  margin-bottom: 0.7rem;
  letter-spacing: 1px;
}
.quick-fact {
  color: #fff;
  font-size: 1rem;
  margin-bottom: 0.4rem;
  text-align: left;
}
.fact-label {
  color: #21a1f3;
  font-weight: 600;
  margin-right: 0.3rem;
}
.fact-value {
  color: #fff;
  font-weight: 400;
}
@media (max-width: 900px) {
  .side-panel {
    display: none;
  }
}
